body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app {
  width: 100vw;
  height: 100vh;
  position: relative;
  overflow: hidden;
}

.background-container {
  width: 100vw;
  height: 100vh;
}

.background-container img {
  width: 100vw;
  height: 100vh;
  object-fit: cover;
}

.main-container {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  width: 80vw;
  height: 370px;
  background-color: rgba(240, 248, 255, 0.9);
  border-radius: 8px;
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.7);
  display: flex;
  flex-direction: column;
  transition: all .2s;
}

.main-container:hover {
  box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.7);
}

.cities-main {
  display: flex;
  width: 100%;
  height: 60px;
  justify-content: center;
  font-size: 16px;
  user-select: none;
  align-items: center;
}

.cities {
  width: 60%;
  height: 60px;
  display: flex;
  align-items: flex-end;
  margin-top: -8px;
  position: absolute;
  overflow-x: scroll;
}

.citi-tab {
  cursor: pointer;
  background-color: aliceblue;
  color: cadetblue;
  padding: 11px;
  margin: 10px;
  border-radius: 0 0 8px 8px;
  box-shadow: 0 0 1px 0 rgba(0, 0, 0, 0.2);
  white-space: nowrap;
}

.active-city {
  background-color: powderblue;
  box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.7);
}

.loading-container {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.weather-main {
  display: flex;
  height: 100%;
}

.weather-options {
  padding: 4px;
  margin: 6px 4px 6px 8px;
  border-radius: 8px;
  background-color: #9ec3e3;
  color: #091f1f;
}

.weather-options div {
  margin-top: 4px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #fff0dc;
  border-radius: 6px;
  padding: 2px;
}

.weather-options div:first-child {
  margin-top: 0;
}

.weather-list {
  display: flex;
  overflow-x: scroll;
  margin: 0 8px 0 0;
}

.one-weather {
  margin: 6px 4px;
  background-color: azure;
  border-radius: 8px;
  padding: 4px;
}

.one-weather div {
  margin-top: 4px;
}
.one-weather div:first-child {
  margin-top: 0;
}

.weather-list-item {
  font-size: 16px;
  height: 20px;
  padding: 2px;
  border-radius: 4px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.weather-date {
  background-color: beige;
  color: #393737;
  white-space: nowrap;
}

.weather-unit {
  height: 40px;
  font-size: 20px;
  background-color: bisque;
  color: #5d5c5c;
}

.weather-unit img {
  width: 60px;
}

.weather-feels {
  font-size: 20px;
  background-color: #fff0dc;
  color: #4c4a4a;
}

.weather-cloudiness {
  height: 22px;
  font-size: 18px;
  background-color: #ccf3ff;
  color: #3f494e;
}

.weather-visibility {
  background-color: #ecb8b6;
  color: #483e3e;
}

.weather-sea-level {
  background-color: #71f3ca;
  color: #4f5855;
}

.weather-ground-level {
  background-color: #d7c3a6;
  color: #3e3c39;
}

.weather-humidity {
  background-color: #5991cf;
  color: #101113;
}

.weather-wind-speed {
  background-color: #be8dff;
  color: #1d1924;
}

.weather-wind-deg {
  background-color: #dacdf6;
  color: #38363c;
}

.languages-container {
  position: absolute;
  right: 8px;
  display: flex;
  justify-content: space-between;
  background-color: #9ec3e3;
  border-radius: 8px;
  color: #ffffff;
}

.languages-container div {
  padding: 8px;
  cursor: pointer;
  border-radius: 8px;
}

.active-lang {
  background-color: #46525d;
}

.search-container {
  position: absolute;
  left: 8px;
  height: 34px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.search-container input {
  height: 14px;
  width: 100px;
  padding: 8px;
  font-size: 18px;
  border: none;
  border-radius: 8px;
  color: #425260;
  transition: all 0.3s;
}

.search-container input:focus {
  outline: none;
  background-color: #eafffb;
}

.dropdown-cities {
  background-color: azure;
  position: absolute;
  top: 36px;
  width: 100px;
  padding: 4px;
  border-radius: 8px;
  color: #1d1924;
}
.dropdown-cities div {
  padding: 4px;
  border-radius: 4px;
  cursor: pointer;
  transition: all 0.2s;
}
.dropdown-cities div:hover {
  background-color: #c0cbd7;
  color: #091f1f;
}
